*{
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  background-color: #d2dbdd;
}
.cards{
  width: 100%;
  height: auto;
}

.card{
  margin: 6%;
  transition: all 0.4s cubic-bezier(0.175, 0.885, 0, 1);
  background-color: #fff;
  width: 21.25%;
  border-radius: 12px;
  box-shadow: 0px 13px 10px -7px rgba(0, 0, 0, 0.1);
  float: left;
}

.card:hover{
  box-shadow: 0px 30px  18px -8px rgba(0, 0, 0, 0.1);
  transform: scale(1.05, 1.05);
}

.card_img{
  width: 100%;
  height: 500px;
  border-top-left-radius: 12px;
  border-top-right-radius: 12px;
}

.card_category{
  font-family: "Raleway", sans-serif;
  text-transform: uppercase;
  font-size: 13px;
  letter-spacing: 2px;
  font-weight: 500;;
  color: #868686;
  
}
.card_title{
  margin-top: 5px;
  margin-bottom: 10px;
  font-family: "Raleway", sans-serif;
  text-transform: capitalize;
}
.card_info{
  font-size: small;
}

.button{
  padding: 5px 8px;
  font-family: "Raleway", "sans-serit";
  text-transform: uppercase;
  font-size: 13px;
  letter-spacing: 2px;
  font-weight: 500;
  color: #868686;
  background-color: #fff;
  outline: none;
  border: 1px solid black;
  cursor: pointer;
}

.heading_style{
  padding: 30px 0;
  text-align: center;
  font-family: "Raleway", "sans-serif";
  text-transform: uppercase;
  font-size: 1.5rem;
  letter-spacing: 2px;
  font-weight: 500;
  color: #868686;
  margin-bottom: 30px;
  background-color: #fff;
}
@media (max-width: 991px){
  .cards{
    width: 100%;
  }
  .card_img{
    height: 300px;
  }
}
@media (max-width: 673px){
  .cards{
    width: 82%;
    margin-left: 8vh;
  }
  .card_img{
    height: 200px;
  }
}
@media (max-width: 375px){
  .cards{
    width: 80%;
    margin:auto;
  }
  .card_img{
    height: 150px;
  }
}